body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

/*--Selectors--*/
html,
body {
  height: 100%;
  width: 100%;
}




/* --Class--*/
/* Nav */
.main-nav a {
  padding-left: 20px;
}

/* Header */
.main-header h1 {
  margin-top: 0;
}



/* BodyBackground */
.main-header {
  background: red;
}

.main-nav {
  background: green;
}

.artists-list {
  background: lightgrey;
}

.bands-list {
  background: papayawhip;
}






/*--ID--*/

/*--Media Queries--*/

/* ListItem component */
.event-container {
  min-height: 4em;
  margin: 10px auto;
  border-top: 1px solid black;
}



.event-container a:hover {
  color: red;

}

.list-item-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  min-height: 40px;
  background-color: white;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.list-item-header h2 {
  padding-right: .7em;
}

.icon-holder {
  height: 2.5em;
  width: 2.5em;
}

.icon {
  height: 100%;
  width: 100%;
}

.list-item-header-link {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  color: black;
  text-decoration: none;
  min-height: 40px;
  height: 100%;
  width: 80%;
  padding: .3em .7em .3em 0;
  text-align: right;
}

.list-item-header-link img {
  object-fit: cover;
  height: 2.5em;
  width: 2.5em;
}

.expand-container {
  line-height: 1.5em;
  max-width: 95%;
  margin: 0 auto;
}

.key {
  text-align:right;
  display: inline-block;
  width: 20%;
}

.space{
  display: inline-block;
  width: 5%;
}

.value {
  display: inline-block;
  width: 75%;
}

.expand-container a {
  text-decoration: none;
  margin: 0 auto;
  transition: color 20ms ease-in;
}

.expand-container div {
  display: -webkit-flex;
  display: flex;
}

